$bg-color: #191919;
$primary-color: #fff;
$title-color: #d20a32;

body {
  background-color: $bg-color;
  color: $primary-color;
  padding: 2%;
  font-family: "Lato";
  width: 1024px;
  margin-left: auto;
  margin-right: auto;
}

a {
  color: inherit;
  text-decoration: inherit;
}

h1 {
  color: $title-color;
  font-size: 48px;
}

.heading--top {
  width: 921px;
  height: 66px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.heading--bottom {
  width: 923px;
  height: 188px;

  .heading--bottom__title {
    color: $primary-color;
    font-size: 48px;
  }

  .heading--bottom__paragraph {
    font-size: 16px;
    color: $primary-color;
  }
}

.games {
  margin-top: 50px;

  .games--dropdown {
    height: 50px;
    width: 384px;
    background: $title-color;
    font-size: 24px;
    color: $primary-color;
    text-align: center;
    display: flex;
    align-items: center;
    padding-left: 25px;
    position: relative;
  }

  .games--dropdown:after {
    content: "◀ ▶";
    font: 17px "Consolas", monospace;
    color: $primary-color;
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
    right: 20px;
    /*Adjustforpositionhoweveryouwant*/
    top: 18px;
    position: absolute;
    pointer-events: none;
    -o-transform: rotate(90deg);
  }

  select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;

    display: block;
    width: 100%;
    max-width: 320px;
    height: 50px;
    float: right;
    padding: 0px 24px;
    -ms-word-break: normal;
    word-break: normal;
    font-family: "Lato";
    background-color: $title-color;
    border: none;
    font-size: 24px;
    color: $primary-color;
  }

  .games--dropdownlogos {
    display: flex;
    flex-direction: column;
  }

  .game--details {
    width: 290px;
    height: 193px;
    position: relative;
  }

  .game--image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    z-index: 1;
  }

  .game--image:hover {
    opacity: 0;
  }

  .game--moredetails {
    position: absolute;
    font-weight: normal;
    width: 100%;
  }

  .game--title {
    margin-top: 5px;
    margin-bottom: 5px;
  }
}
.articles {
  display: flex;
  flex-wrap: wrap;
}

.cardGame {
  width: 33%;
  margin-bottom: 20px;
}

.game--tags {
  font-size: 13px;
  overflow: hidden;
  max-width: 100%;
}

.game--feed {
  width: 1135px;
  height: 895px;
}

/* Site Search */

.search-module {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}

#site-search {
  height: 50px;
  width: 50px;
  border-style: none;
  padding: 10px;
  font-size: 18px;
  letter-spacing: 2px;
  outline: none;
  border-radius: 25px;
  transition: all 0.5s ease-in-out;
  padding-right: 40px;
  color: $primary-color;
  background-color: transparent;
}
#site-search::placeholder {
  color: #616161;
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 100;
}
#search-button {
  width: 50px;
  height: 50px;
  border-style: none;
  font-size: 20px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  border-radius: 50%;
  position: absolute;
  right: 0px;
  color: $primary-color;
  background-color: transparent;
  pointer-events: painted;
}
#search-button:focus ~ #site-search {
  width: 300px;
  height: 40px;
  padding: 10px;
  border-radius: 50px;
  background-color: transparent;
  border: 1px solid $primary-color;
  transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  -o-border-radius: 50px;
}
#site-search:focus {
  width: 300px;
  height: 40px;
  padding: 10px;
  padding-left: 30px;
  border-radius: 50px;
  background-color: transparent;
  border: 1px solid $primary-color;
  transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
}

.game-more {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
}

#show-more {
  background: $title-color;
  width: 223px;
  height: 51px;
  font-size: 24px;
  color: $primary-color;
  text-align: center;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  border: none;
}

.page-hero {
  width: 100%;
  height: 419px;
  left: -13px;
  top: 147px;
}

.page-list {
  margin-top: 30px;
}

.hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  left: 0px;
  top: 31px;
  z-index: -1;
}

.article--top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.article--bottom {
  display: flex;
  flex-wrap: wrap;
}

.title {
  color: $primary-color;
}

.rating {
  height: 29px;
  top: calc(50% - 29px / 2 - 141.5px);
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: $title-color;
}

.check-website {
  position: absolute;
  width: 228px;
  height: 50px;
  left: 60%;
  top: 473px;
  background: $title-color;
  color: $primary-color;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  z-index: 2;
  padding-left: 20px;
  padding-right: 20px;
  font-weight: bold;
  font-size: 16px;
}

.store-links {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: $primary-color;
  text-decoration: underline $title-color;
  text-decoration-thickness: 3px;
}

.screenshots-links {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 100%;
}
.screenshots-img {
  width: 48%;
}
